import React, { Component } from 'react';

import '../NewApp.css';

import { Button, Col } from 'antd';

class Menu extends Component {
    render() { 
        return ( 
            <> 
                <div className='intro' style={this.props.clickedText ? {display : 'none'} : {dispay : 'block'}}>
                    <p>
                    Bienvenue dans la version 1 de mon application de liste de course interactive!
                    Tout d'abord merci pour le temps que vous me consacrer afin que je puisse l'améliorer!
                    Il s'agit d'une application de liste de course qui permet/permettra de faire ses courses
                    plus rapidement seul ou à plusieurs.
                    </p>
                    <p>
                    On peut y mettre des commentaires, trier par catégorie, et bientôt pleins d'autres options.
                    </p>
                    <p>
                    Le desgin de l'application n'est pas fait...désolé si ce n'est pas encore très beau, je me
                    concentre sur les fonctionnalités.
                    </p>
                    <p>
                    Pour le moment, une personne par compte, "mon profile" n'est pas encore fonctionnel,
                    "mes listes" et "en course" sont quasi identique mais ça va changer.
                    </p>
                    <p>
                    Merci de noter si vous rencontrez des difficultés, ce qui fonctionne et ce qui ne fonctionne
                    pas, ce que vous ne comprennez pas et au contraire ce qui est très clair. Ca m'aidera
                    dans l'amélioration de cette application!
                    </p>
                        <Col span={6} push={18}>
                            <Button block type="primary" onClick={this.props.hideText} className='goBack'>
                                Fermer
                            </Button>
                        </Col>
                </div>
                        <Button 
                            style={{ margin : '1em 0'}}
                            size='large'
                            block 
                            type="primary"
                            className='item-button'
                            name='enCourse'
                            onClick={this.props.choixComponent}
                            >En course!
                        </Button>
                        <Button 
                            style={{ margin : '0 0 1em 0'}}
                            size='large'
                            block 
                            type="primary"
                            className='item-button'
                            name='mesListes'
                            onClick={this.props.choixComponent}
                        >Créer une liste
                        </Button>
                        <Button 
                            style={{ margin : '0 0 1em 0'}}
                            size='large'
                            block 
                            type="primary" 
                            className='item-button'
                            name='monProfile'
                            onClick={this.props.choixComponent}
                        >Mon profil
                        </Button>
            </>
         );
    }
}
 
export default Menu;