import React, { Component } from 'react';

import { Col, List, Row, Icon, Tooltip, Button, Badge  } from 'antd';

class EnCourseListItem extends Component {
    constructor(props) {
        super(props);
        this.state = {  }
    }
    render() { 
        const currentTitre = this.props.currentTitre;
        const data = this.props.data;
        const arrayData = [];
        if(!data) {
            return null
        }
        Object.keys(data).map(item => item === currentTitre ? Object.keys(data[item]).map(category => Object.keys(data[item][category]).map(categoryItem => arrayData.push(data[item][category][categoryItem]))): null);
        arrayData.reverse();
        const liste = (arrayData || []).map((elt, id) => 
            <List
                key={`listId-${id}`}
                id={elt.titreCat}
                header={
                    <Row type="flex" justify="space-between" align='middle' style={{height: '2.5em'}}>
                        <Col span={24} className='titreCat' onClick={() => this.props.checkCurrentCategory(elt.titreCat)}>
                            {elt.titreCat}
                        </Col>
                    </Row>
                }
                bordered
                dataSource={Object.keys(elt.categoryItems || []).map(item => elt.categoryItems[item]).reverse()}
                renderItem={(item, id) => (
                    <List.Item key={`listItemId-${id}`} id={elt.titreCat} className={item.checked ? '.ant-list-item flip' : '.ant-list-item'}>
                            <Col span={24} >
                                <Row 
                                    type="flex" 
                                    justify="space-between" 
                                    align="middle"
                                    key={item.key}
                                    id={`elt-${item.key}`}
                                >
                                    <Col xs={20} sm={21} md={21} lg={22} onClick={() => this.props.isChecked(item, id)} className={'ant-list-items-item'}>
                                        {item.item}
                                    </Col>
                                    <Col xs={4} sm={3} md={3} lg={2}>
                                        <Tooltip placement="top" title='Commentaire' mouseEnterDelay={0.5}>
                                            <Button 
                                                name="comment" 
                                                className='comment' 
                                                style={{ background: 'transparent', border: 'none' }}
                                                type='primary'
                                                // size='large'
                                                block
                                                onClick={() => this.props.toggleComment(item)}>
                                                {
                                                    item.comment === '' ?
                                                    <Icon type='message' style={{color: '#009688' , fontSize: '1.4em' }}/>
                                                    :
                                                    <Badge status="success">
                                                        <Icon type='message' style={{color: '#009688' , fontSize: '1.4em' }}/>
                                                    </Badge>
                                                }
                                            </Button>
                                        </Tooltip>
                                    </Col>
                                </Row>
                                <Col span={24} className={item.show ? 'form-comm-container-active' : 'form-comm-container'}>
                                    <form className='form-comm' onSubmit={e => this.props.closeComment(e, item)}>
                                        <input 
                                            value={item.comment} 
                                            name='comment' 
                                            type='text'
                                            id='inputComment'
                                            placeholder='ajouter un commentaire'
                                            onChange={(e) => this.props.addComment(e, item)}
                                            className={item.show ? 'item message-enter-active' : 'item message-leave-active'}
                                            maxLength={50}
                                        />
                                    </form>
                                </Col>
                            </Col>
                    </List.Item>
                    )}
            />
    
    );
        return ( 
            <>
                <Col xs={{ span: 24 }} md={{ span: 22, push: 1 }} lg={{ span: 20, push: 2 }}>
                    <form className='form form-titre enCourse-form-titre' onSubmit={this.props.createNewItemLastMinute}>
                        <input type='text' placeholder='Entrez un produit' maxLength={40} />
                        <button type='submit' id='buttonSubmit'>+</button>
                    </form>
                </Col>
                <Col span={24}>  
                    {liste}
                </Col>
            </>
         );
    }
}
 
export default EnCourseListItem;