import React, { Component } from 'react';

import { Button, Icon, Row, Col, notification, Switch } from 'antd';

const openNotification = () => {
    notification.open({
        message: 'Votre profil',
        description: "Bientôt vous aurez la possibilité de mofifier votre profil. D'ajouter un autre membre de la famille et bien plus encore!",
        duration: 0,
        style: {
            textAlign: 'justify'
          },
        // onClose: () => {
        //   alert('clicked');
        // },
      });
  };

class ProfilUser extends Component {
    componentDidMount() {
        if(this.props.help) {
            openNotification();
        }
    }
    render() { 
        const user = this.props.user;
        return ( 
            <div>
                <Row type="flex" justify="center">
                    <Col  xs={{ span: 24 }} md={{ span: 22 }} lg={{ span: 20 }}>
                    <h2>Bienvenue sur votre profile {user}</h2>
                    </Col>
                </Row>
                <Row type="flex" justify="center">
                    <Col xs={{ span: 24 }} md={{ span: 22 }} lg={{ span: 20 }}>
                        <span>Couper l'aide: </span>
                        <Switch onChange={this.props.toggleSwitchHelp} id='switchButtonHelp'/>
                    </Col>
                </Row>
                <Row type='flex' justify='left'>
                    <Col span={6}>
                    <div className='button-anth'>
                        <Button block type="primary" onClick={this.props.returnToApp} className='goBack'>
                            <Icon type="left" />Retour
                        </Button>
                    </div>
                    </Col>
                </Row>
            </div>
         );
    }
}
 
export default ProfilUser;