import React, { Component } from 'react';

import { List, Row, Col, Icon, Tooltip, Button, Badge } from 'antd';

class ItemList extends Component {
    constructor(props) {
        super(props);
        this.state = {  }
    }
    render() { 
        // const indexItem = this.props.indexItem;
        // const numChildCategory = this.props.numChildCategory;
        const currentTitre = this.props.currentTitre;
        const data = this.props.data;
        const arrayData = [];
        if(!data) {
            return null
        }
        Object.keys(data).map(item => item === currentTitre ? Object.keys(data[item]).map(category => Object.keys(data[item][category]).map(categoryItem => arrayData.push(data[item][category][categoryItem]))): null);
        arrayData.reverse();
        const liste = (arrayData || []).map((elt, id) => 
            <List
                key={`listId-${id}`}
                id={elt.titreCat}
                header={
                    <Row type="flex" justify="space-between" align='middle'>
                        <Col xs={18} md={20}  lg={22} className='titreCat' onClick={() => this.props.getCurrentCategory(elt.titreCat)}>
                            {elt.titreCat}
                        </Col>
                        <Col xs={3} md={2} lg={1}>
                            <input type='checkbox' id={elt.titreCat}/>
                        </Col>
                        <Col xs={3} md={2} lg={1}>
                            <button 
                            name="trash" 
                            className="trash" 
                            onClick={() => this.props.deleteCategory(elt, id)}>
                                <Icon type='delete' style={{ color : '#FFF', fontSize: '1.4em' }}/>
                            </button>
                        </Col>
                    </Row>
                }
                bordered
                dataSource={Object.keys(elt.categoryItems || []).map(item => elt.categoryItems[item]).reverse()}
                renderItem={(item, id) => (
                    <List.Item key={`listItemId-${id}`} id={elt.titreCat} className={item.checked ? '.ant-list-item flip' : '.ant-list-item'}>
                        {/* <Row type="flex" justify="space-around" align="middle" className={indexItem === `elt-${item.item}` && numChildCategory > 1 ? 'message-leave': ''} id={`elt-${item.key}`} key={item.key}> */}
                            <Col span={24} >
                                <Row 
                                    type="flex" 
                                    justify="space-between" 
                                    align="middle"
                                    // className={item.checked ? '.ant-list-bordered .ant-list-item flip' : '.ant-list-bordered .ant-list-item'} 
                                    key={item.key}
                                    id={`elt-${item.key}`}
                                >
                                    <Col xs={16} sm={18} md={18} lg={20} onClick={() => this.props.isChecked(item, id)} className={'ant-list-items-item'}>
                                        {item.item}
                                    </Col>
                                    <Col xs={4} sm={3} md={3} lg={2}>
                                        <Tooltip placement="top" title='Supprimer' mouseEnterDelay={0.5}>
                                            <Button 
                                                style={{ background: 'transparent' }}
                                                name="trash" 
                                                className="trash"
                                                type='primary'
                                                // size='large'
                                                block
                                                onClick={() => this.props.delete(item, id)}>
                                                    <Icon type='delete' style={{ color : '#009688', fontSize: '1.4em' }}/>
                                            </Button>
                                        </Tooltip>
                                    </Col>
                                    <Col xs={4} sm={3} md={3} lg={2}>
                                        <Tooltip placement="top" title='Commentaire' mouseEnterDelay={0.5}>
                                            <Button 
                                                name="comment" 
                                                className='comment' 
                                                style={{ background: 'transparent', border: 'none' }}
                                                type='primary'
                                                // size='large'
                                                block
                                                onClick={() => this.props.toggleComment(item)}>
                                                {
                                                    item.comment === '' ?
                                                    <Icon type='message' style={{color: '#009688' , fontSize: '1.4em' }}/>
                                                    :
                                                    <Badge status="success">
                                                        <Icon type='message' style={{color: '#009688' , fontSize: '1.4em' }}/>
                                                    </Badge>
                                                }
                                            </Button>
                                        </Tooltip>
                                    </Col>
                                </Row>
                                <Col span={24} className={item.show ? 'form-comm-container-active' : 'form-comm-container'}>
                                    <form className='form-comm' onSubmit={e => this.props.closeComment(e, item)}>
                                        <input 
                                            value={item.comment} 
                                            name='comment' 
                                            type='text'
                                            id='inputComment'
                                            placeholder='ajouter un commentaire'
                                            onChange={(e) => this.props.addComment(e, item)}
                                            className={item.show ? 'item message-enter-active' : 'item message-leave-active'}
                                            maxLength={50}
                                        />
                                    </form>
                                </Col>
                            </Col>
                                
                        {/* </Row> */}
                    </List.Item>
                    )}
            />
    
    );
        return ( 
            <>
            {liste}
            </>
         );
    }
}
 
export default ItemList;